  top: -10px;
  right: -1px;
}
.app-topbar ul {
  list-style-type: none;
  margin-bottom: 0;
}

.logo-topbar {
  float: left;
  display: none;
  line-height: var(--ins-topbar-height);
  padding: 0 0.625rem;
}
.logo-topbar .logo-sm {
  display: none;
}
